import { __ } from '@wordpress/i18n';

const SearchFilter = ({ handleChange, searchValue }) => {
    return (
        <div className="relative mt-5 rounded-md shadow-sm">
            <input type="text" placeholder={ __( 'Search your documentation', 'wedocs' ) } value={searchValue} onChange={handleChange}
                className="w-80 h-10 text-sm focus:!border-indigo-300 !pl-3 !rounded-md !border-gray-300" />
            { searchValue && searchValue.length > 0 &&
                <div onClick={ (e) => handleChange( e, true ) } className="cursor-pointer absolute inset-y-0 right-0 flex items-center pr-3">
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-5 h-5">
                        <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
                    </svg>
                </div>
            }
        </div>
    );
}

export default SearchFilter;
